import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { DownloadService } from '../../angular-services/download.service';
import { DownloadCategoryService } from '../../angular-services/download-category.service';
import { DownloadCategory } from '../../classes/download-category';
import { Download } from '../../classes/download';

@Component({
  selector: 'app-edit-download-form-admin',
  templateUrl: './edit-download-form.component.html',
  styleUrls: ['./edit-download-form.component.css']
})
export class EditDownloadFormComponent implements OnInit {

  @Output() downloadTitle = new EventEmitter<string>();
  formEditDownload: FormGroup;
  alert: any = { type: '', message: '', show: false };
  categories: DownloadCategory[] = [];
  download: Download = new Download();
  mediaType = 'downloads';
  urlObjects: string = 'https://viruscursinho.com.br:3002/downloads';
  showModalSelectFile: boolean = false;

  constructor(private downloadService: DownloadService, private categoryService: DownloadCategoryService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getCategories();
    this.getDownload();
  }

  initForm() {
    this.formEditDownload = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      file: new FormControl(''),
      idCategory: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getDownload() {
    let key = this.route.snapshot.paramMap.get('id');
    this.downloadService.getDownload(key).subscribe(
      response => {
        if (response !== undefined) {
          this.download = response.download;
          this.download.idCategory = response.category._key;
          this.downloadTitle.emit(this.download.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditDownload.patchValue({
      title: this.download.title,
      description: this.download.description,
      idCategory: this.download.idCategory,
      status: this.download.status+'',
      file: this.download.file
    });
  }

  save() {
    if (this.formEditDownload.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditDownload.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.downloadService.updateDownload(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getCategories() {
    this.categoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          for (let category of response) {
            if (category.status) {
              this.categories.push(category);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formEditDownload.patchValue({
      file: link
    });
    this.showModalSelectFile = false;
  }

}
