import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { EditDownloadPageComponent } from './edit-download-page.component';

const routes: Routes = [
  { path: '', component: EditDownloadPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class EditDownloadPageRoutingModule { }
